.gameWrapper {
    text-align: center;
    position: relative;
}

.center {
    display: inline-block;
}

.scoreBoardFloating {
    position: relative;
    display: inline-block;
    width: 0;
    height: 0;
    vertical-align: top;
}

.scoreBoard {
    display: inline-block;
    position: absolute;
    width: 20rem;
    text-align: left;
    padding-left: 1rem;
}
